import { notFound } from 'next/navigation'
import { getProductById } from '@/lib/data'
import { formatPrice } from '@/lib/utils'
import Image from 'next/image'
import Link from 'next/link'

interface ProductPageProps {
  params: {
    id: string
  }
}

export default function ProductPage({ params }: ProductPageProps) {
  const product = getProductById(params.id)
  
  if (!product) {
    notFound()
  }
  
  return (
    <div className="min-h-screen bg-white">
      {/* Header */}
      <header className="bg-white shadow-sm border-b border-gray-200">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex items-center justify-between h-16">
            <Link href="/" className="text-2xl font-semibold text-black">
              TechStore
            </Link>
            <div className="flex items-center space-x-4">
              <Link 
                href="/"
                className="text-sm text-black hover:text-red-600 transition-colors"
              >
                ← Back to Products
              </Link>
            </div>
          </div>
        </div>
      </header>

      {/* Breadcrumb */}
      <nav className="bg-white border-b border-gray-200">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex items-center h-12 text-sm">
            <Link href="/" className="text-gray-700 hover:text-black">Home</Link>
            <span className="mx-2 text-gray-400">/</span>
            <Link href="/" className="text-gray-700 hover:text-black">Products</Link>
            <span className="mx-2 text-gray-400">/</span>
            <span className="text-red-600 font-medium">{product.name}</span>
          </div>
        </div>
      </nav>

      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <div className="bg-white rounded-lg shadow-sm overflow-hidden">
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-8 p-8">
            {/* Product Image */}
            <div className="bg-gray-50 rounded-lg p-8 aspect-square relative">
              <Image
                src={product.image}
                alt={`${product.name} product image`}
                fill
                className="object-contain"
                sizes="(max-width: 1024px) 100vw, 50vw"
              />
            </div>
            
            {/* Product Details */}
            <div className="space-y-6">
              <div>
                <h1 className="text-3xl font-bold text-gray-900 mb-2">
                  {product.name}
                </h1>
                <p className="text-gray-600 text-lg">
                  {product.description}
                </p>
              </div>
              
              <div className="flex items-center space-x-4">
                <span className="text-3xl font-bold text-red-600">
                  {formatPrice(product.price)}
                </span>
                <div className="flex items-center text-sm text-gray-500">
                  <span className="text-yellow-400 mr-1">★</span>
                  <span>{product.rating.average.toFixed(1)}</span>
                  <span className="ml-1">({product.rating.count} reviews)</span>
                </div>
              </div>
              
              <div className="border-t pt-6">
                <div className="flex items-center justify-between mb-4">
                  <span className="text-sm font-medium text-gray-700">
                    Availability:
                  </span>
                  <span className={`text-sm px-3 py-1 rounded-full ${
                    product.inStock 
                      ? 'bg-green-100 text-green-800' 
                      : 'bg-red-100 text-red-800'
                  }`}>
                    {product.inStock ? 'In Stock' : 'Out of Stock'}
                  </span>
                </div>
                
                <button 
                  className={`w-full py-3 px-6 rounded-md text-lg font-medium transition-colors ${
                    product.inStock
                      ? 'bg-red-600 text-white hover:bg-red-700 focus:ring-2 focus:ring-red-500 focus:ring-offset-2'
                      : 'bg-gray-300 text-gray-500 cursor-not-allowed'
                  }`}
                  disabled={!product.inStock}
                  aria-label={product.inStock ? 'Add to cart' : 'Out of stock'}
                >
                  {product.inStock ? 'Add to Cart' : 'Out of Stock'}
                </button>
              </div>
              
              <div className="border-t pt-6">
                <h3 className="text-lg font-semibold text-gray-900 mb-3">
                  Product Details
                </h3>
                <dl className="space-y-2">
                  <div className="flex justify-between">
                    <dt className="text-sm text-gray-600">Category:</dt>
                    <dd className="text-sm font-medium text-gray-900 capitalize">
                      {product.categoryId}
                    </dd>
                  </div>
                  <div className="flex justify-between">
                    <dt className="text-sm text-gray-600">Product ID:</dt>
                    <dd className="text-sm font-medium text-gray-900">
                      {product.id}
                    </dd>
                  </div>
                </dl>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
